body {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  display: flex;
}

label {
  flex-direction: column;
  justify-content: center;
  gap: 15px;
  display: flex;
}

#user-output {
  width: 90%;
  height: 100px;
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  top: 0;
  left: 0;
}

#user-output span {
  border: 1px solid #000;
  margin-right: 5px;
  padding: 5px;
}

#user-input {
  margin-bottom: 10px;
}

.highlighted {
  cursor: move;
  color: red;
  background-color: #ff0;
  padding: 5px;
  position: absolute;
}

.active {
  color: green;
}

#draggable {
  width: 100px;
  height: 100px;
  color: #fff;
  cursor: pointer;
  background-color: #00f;
  position: absolute;
}

.item.dragging :where(#user-output, span) {
  opacity: 0;
}

.selected {
  color: #00f;
  font-size: 18px;
}

.combined {
  color: green;
  font-size: 18px;
}

